import { useEffect, useState } from 'react'

export const useParallax = (e) => {

	const [parallaxX, setParallaxX] = useState(0);

	const [parallaxY, setParallaxY] = useState(0);

	const handleScrollX = (e) => setParallaxX((window.innerWidth - e.pageX*-5)/300);

	const handleScrollY = (e) => setParallaxY((window.innerHeight - e.pageY*-5)/200);

	useEffect(() => {
		window.addEventListener('mousemove', handleScrollX);
		window.addEventListener('mousemove', handleScrollY);
	}, []);

    return [parallaxX, parallaxY];
}

