import React from 'react';
import { useParallax } from '../../helpers/useParallax';

export const HomeScreen = (e) => {
	
	const [parallaxX, parallaxY] = useParallax(e);

	return (
		<div className="home__home-container animate__animated animate__fadeInUp">
			{/* <div className="home__aside" /> */}
			<div className="home__home-main">
				{/* <div className="home__home-line-out" /> */}
				<div className="home__title-container">
					<div className="home__name">
						<h1>David</h1>
					</div>
					<div className="home__lastname">Caro</div>
				</div>
				<div className="home__home-line-in" />
				<div className="home__home-img-container">
					<img
						className="home__home-logo"
						src="assets/logo/logo.png"
						alt="logo" 
						style={{transform: `translateX(${parallaxX}px) translateY(${parallaxY}px)`}}
						/>
				</div>
				<div className="home__home-line-in" />
				<div className="home__portfolio-box">
					<h2>Portfolio</h2>
				</div>
				{/* <div className="home__home-line-out" /> */}
			</div>
			{/* <div className="home__aside" /> */}
		</div>
	)
}
